.modal {
    display: none;
    position: fixed;
    z-index: 10000;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgb(0, 0, 0);
    background-color: rgba(0, 0, 0, 0.4);
}

.modal-content {
    background-color: #EAF2FE;
    margin: 10% auto;
    padding: 20px;
    border: 1px solid #888;
    width: 70%;
    color: #307DF6;
    font-family: Arial, Helvetica, sans-serif;
}

.modal-content h1 {
    color: #307DF6;
    font-size: 70px;
}

.modal-content p {
    font-size: 25px;
}

.close {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
}

.close:hover,
.close:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
}